.settings_panel__container {
    display: flex;
    flex-direction: column;
    gap: 5px;
    align-items: center;
    padding: 5px 10px;
    border-radius: 5px;
}
.settings_panel__wait_image {
    width: 300px;
    height: 200px;
    border: 2px dashed #ccc;
    border-radius: 5px;
}
.settings_panel__drawing{
    margin: 10px 20px;
}

.settings_panel__image_upload {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.settings_panel__button_clear {
    border: none;
    border-radius: 5px;
    padding: 5px 10px;
    font-size: 16px;
    background-color: #f44336;
    color: white;
    cursor: pointer;
    margin-bottom: 20px;
}
.settings_panel__button_clear:hover {
    border: 1px solid #f44336;
    background-color: white;
    color: #f44336;
}
@media (max-width: 1280px) {
    .settings_panel__container_config{
        display: flex;
        align-items: center;
        position: relative;
    }
}

@media (max-width: 768px) {
    .settings_panel__container_config{
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
    }
    .settings_panel__button_clear {
        padding: 5px;
        margin-bottom: 5px;
    }

}